@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -webkit-touch-callout: none; -khtml-user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; display: block; overflow: hidden; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; top: 0; left: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { display: table; content: ''; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { display: none; float: left; height: 100%; min-height: 1px; }

[dir='rtl'] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/* Slider */
.slick-loading .slick-list { background: #fff url("./ajax-loader.gif") center center no-repeat; }

/* Icons */
@font-face { font-family: 'slick'; font-weight: normal; font-style: normal; src: url("./fonts/slick.eot"); src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg"); }

/* Arrows */
.slick-prev, .slick-next { font-size: 0; line-height: 0; position: absolute; top: 50%; display: block; width: 20px; height: 20px; padding: 0; -webkit-transform: translate(0, -50%); -ms-transform: translate(0, -50%); transform: translate(0, -50%); cursor: pointer; color: transparent; border: none; outline: none; background: transparent; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { color: transparent; outline: none; background: transparent; }

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before { opacity: 1; }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { opacity: .25; }

.slick-prev:before, .slick-next:before { font-family: 'slick'; font-size: 20px; line-height: 1; opacity: .75; color: white; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-prev { left: -25px; }

[dir='rtl'] .slick-prev { right: -25px; left: auto; }

.slick-prev:before { content: '←'; }

[dir='rtl'] .slick-prev:before { content: '→'; }

.slick-next { right: -25px; }

[dir='rtl'] .slick-next { right: auto; left: -25px; }

.slick-next:before { content: '→'; }

[dir='rtl'] .slick-next:before { content: '←'; }

/* Dots */
.slick-dotted.slick-slider { margin-bottom: 30px; }

.slick-dots { position: absolute; bottom: -25px; display: block; width: 100%; padding: 0; margin: 0; list-style: none; text-align: center; }

.slick-dots li { position: relative; display: inline-block; width: 20px; height: 20px; margin: 0 5px; padding: 0; cursor: pointer; }

.slick-dots li button { font-size: 0; line-height: 0; display: block; width: 20px; height: 20px; padding: 5px; cursor: pointer; color: transparent; border: 0; outline: none; background: transparent; }

.slick-dots li button:hover, .slick-dots li button:focus { outline: none; }

.slick-dots li button:hover:before, .slick-dots li button:focus:before { opacity: 1; }

.slick-dots li button:before { font-family: 'slick'; font-size: 6px; line-height: 20px; position: absolute; top: 0; left: 0; width: 20px; height: 20px; content: '•'; text-align: center; opacity: .25; color: black; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before { opacity: .75; color: black; }

@font-face { font-family: Bebas; src: url(../fonts/bebas.ttf); }

a { text-decoration: none; }

h1 { font-family: Bebas; font-size: 35px; line-height: 1.166666667; font-weight: bold; color: #333333; margin: 23px 0 0 0; }

h2 { color: #333333; font-family: Roboto; font-size: 24px; font-weight: bold; line-height: 33.6px; margin-bottom: 24px; margin-top: 24px; }

h3 { font-family: Roboto; font-size: 1rem; color: #666666; font-weight: 400; margin-top: 18px; margin-bottom: .9375rem; }

h4 { color: #2c7ef0; font-family: Roboto; font-size: 18px; line-height: 24px; font-weight: 400; margin-bottom: 8px; }

h4:hover { color: #1552a8; }

.bold { font-weight: 700; }

.sponsored { background-color: #ebebeb; color: #333333; font-family: Roboto; font-size: 10px; font-weight: 500; vertical-align: middle; margin-right: .25rem; line-height: 1; padding: .25rem .5rem .25rem; height: 20px; margin-top: 10px; }

.sponsored-by img { height: 40px; }

.sponsored-by { display: flex; margin-bottom: 4px; }

.pagehead, .aside, .mainblock { margin: 27px auto; width: 92%; }

.breadcrumbs a { font-family: Roboto; font-size: 12px; text-decoration: none; text-transform: uppercase; color: #aaaaaa; }

.breadcrumbs_lastlink::before { color: #aaa; content: "/"; margin: 0 .63rem; position: relative; top: 1px; }

.breadcrumbs a:hover { color: #9c9c9c; }

.author { font-family: Roboto; font-size: 14px; color: #666666; margin-top: 20px; margin-bottom: 25px; }

.clearfix:after { content: ""; display: table; clear: both; }

.authorpic, .follow { display: none; }

.author-profile { color: #2c7ef0; }

.author__time::before { content: "|"; margin: 0 5px; position: relative; top: 1px; }

.article { margin: 0 auto; color: #333333; font-family: Roboto; font-size: 16px; font-weight: 400; line-height: 24px; }

.article__img img { width: 100%; border: 1px solid #ddd; }

.article p { padding-bottom: 25px; }

.article__imgcaption { color: #666666; border-bottom: 1px solid #dcdfe8; font-family: Roboto; font-size: 14px; padding-bottom: 25px; margin-top: 40px; margin-bottom: 25px; }

.article__imgcaption__source { font-size: 12px; }

.article a, .aside a { color: #2c7ef0; }

.article a:hover, .author-profile:hover, .aside a:hover { color: #005EA6; }

.article__ad { font-weight: 700; margin-bottom: 20px; }

.article__paragraphhead { font-weight: 700; }

.article__about-author { font-style: italic; }

.aside__about { border-top: 1px solid #ddd; }

.aside__about p, .aside__investing p { color: #333333; font-family: Roboto; font-size: 16px; font-weight: 400; line-height: 24px; margin-bottom: 20px; }

.aside__investing-img { width: 100%; margin-bottom: 15px; }

.aside__investing { margin-bottom: 24px; border-bottom: 1px solid #dcdfe8; }

.slick-dots li button:before { content: ''; border: 1px solid #2c7ef0; border-radius: 24px; }

@media (min-width: 1024px) { .author { font-family: Roboto; font-size: 14px; border: 1px solid #ddd; border-right: 0; height: 52px; }
  .author__title { margin-top: 16px; margin-left: 64px; }
  .authorpic { display: block; float: left; }
  .authorpic img { width: 50px; }
  .follow { display: block; float: right; margin-top: -17px; }
  .follow img { margin: 0 4px 7px 0; }
  .mailpic { padding-bottom: 2px; }
  .wrap { display: flex; margin: 27px auto; width: 74%; }
  .mainblock { width: 64%; }
  .pagehead { margin: 27px auto; width: 74%; }
  .aside { width: 35%; margin: 0; padding: 0 0 0 30px; }
  .aside__about { border-top: 0; border-bottom: 1px solid #ddd; padding-bottom: 20px; margin-bottom: 24px; } }

a { color: #000; }

.header { background-color: #13325D; color: #c0cadd; font-weight: 400; font-family: Roboto; font-size: 1rem; padding-top: 8px; }

.header ul { margin: 0; padding: 0; list-style: none; overflow: hidden; }

.header li a { display: block; text-decoration: none; color: #c0cadd; line-height: 1.2; margin-top: 11px; padding-bottom: 16px; }

.menu__item:hover { color: white; box-shadow: inset 0 -3px 0 0 #2c7ef0; }

.menu-btn:hover { background-color: white; }

.header__logo { display: block; float: left; text-decoration: none; font-size: 40px; margin-left: 15px; }

.header__logo img { height: 32px; width: 120px; }

.menu { clear: both; max-height: 0; transition: max-height .2s ease-out; text-align: center; }

.menu-icon { cursor: pointer; display: inline-block; float: right; padding: 20px 20px; position: relative; user-select: none; }

.navicon { background: white; display: block; height: 2px; position: relative; transition: background .2s ease-out; width: 18px; }

.navicon:before, .navicon:after { background: white; content: ''; display: block; height: 100%; position: absolute; transition: all .2s ease-out; width: 100%; }

.navicon:before { top: 5px; }

.navicon:after { top: -5px; }

.menu-btn { display: none; }

.menu-btn:checked ~ .menu { max-height: 312px; }

.menu-btn:checked ~ .menu-icon .navicon { background: transparent; }

.menu-btn:checked ~ .menu-icon .navicon:before { transform: rotate(-45deg); }

.menu-btn:checked ~ .menu-icon .navicon:after { transform: rotate(45deg); }

.menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before, .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after { top: 0; }

.menu__lastitem { margin-bottom: 32px; }

.header:after { content: ""; display: table; clear: both; }

.menu__item { margin-left: 28px; }

.menu__itemfirst { margin-left: 14px; }

.header li .menu__category { font-weight: bold; color: white; font-size: 18px; margin-left: 7px; }

@media (min-width: 1084px) { .header { margin: 0 auto; }
  .header li { float: left; }
  .menu { clear: none; max-height: none; } }

.footer { background-color: #13325D; padding: 0 15px; color: #c0cadd; font-family: Roboto; font-size: 12px; height: 110px; }

.footer__nav { display: flex; margin-bottom: 4px; }

.footer__nav a { font-weight: 400; line-height: 24px; color: #c0cadd; font-family: Roboto; font-size: 16px; }

.footer__policy a { font-family: Roboto; font-size: 12px; line-height: 18px; color: #c0cadd; text-decoration: underline; margin-right: 5px; }

.footer__navitem { margin-top: 20px; margin-right: 20px; }

.footer__forfullscr { display: none; }

.footer__logo { margin-top: 15px; margin-right: 22px; display: none; }

.footer__logo img { width: 226px; }

.footer__navitem:hover, .footer__policy a:hover { color: white; }

@media (min-width: 640px) { .footer__forfullscr { display: block; } }

@media (min-width: 1024px) { .footer { display: flex; justify-content: center; }
  .footer__logo { display: block; } }
