.footer {
  background-color: #13325D;
  padding: 0 15px;
  color: rgb(192, 202, 221);
  @include default-font(12px);
  height: 110px; }

.footer__nav {
  display: flex;
  margin-bottom: 4px; }

.footer__nav a {
  font-weight: 400;
  line-height: 24px;
  color: rgb(192, 202, 221);
  @include default-font(16px); }

.footer__policy a {
  @include default-font(12px);
  line-height: 18px;
  color: rgb(192, 202, 221);
  text-decoration: underline;
  margin-right: 5px; }

.footer__navitem {
  margin-top: 20px;
  margin-right: 20px; }

.footer__forfullscr {
  display: none; }

.footer__logo {
  margin-top: 15px;
  margin-right: 22px;
  display: none; }

.footer__logo img {
  width: 226px; }

.footer__navitem:hover, .footer__policy a:hover {
  color: white; }

@media (min-width: 640px) {
  .footer__forfullscr {
    display: block; } }

@media (min-width: 1024px) {
  .footer {
    display: flex;
    justify-content: center; }

  .footer__logo {
    display: block; } }
